.personal-sidebar{
    background: linear-gradient(#2B4173,#48629e 80%);
    position: sticky;
    top:0;
    left:0;
    width:280px;
    max-width:77.5%;
    z-index:1000;
    overflow-y: auto;
    box-shadow: 1px 0px 5px rgba(0,0,0,0.3);
    min-height: 100vh;
    transition: 0.15s ease-in-out;
}
.personal-sidebar::-webkit-scrollbar {
    width: 7px;
}
.personal-sidebar::-webkit-scrollbar-track {
    background: #e2e4e7;
    border-radius: 15px;
}
.personal-sidebar::-webkit-scrollbar-thumb {
    background-color:  #a3a9c4;
    border-radius: 15px;
}

.personal-sidebar .logo-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin:25px 20px;
    a{
        display: flex;
        align-items: center;
    }
    .logo{
        content: url('../../assets/Navbar/blue-logo.png');
        width: 30px;
        margin-right: 7.5px;
    }
    h6{
        color:#fff;
        font-size: 17px;
    }
    button{
        width: 33px;
        height: 33px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 0;
        cursor: pointer;
        background:#4a629e;
        font-size: 27px;
        color:#fff;
        svg{
            margin:1px 1px 0 0;
        }
    }
}

.personal-sidebar .link-cont{
    margin:2px 0;
    a{
        border-left:6px solid transparent;
        padding:9px 10px 9px 25px;
        display: flex;
        align-items: center;
        color:#eee;
        svg{
            font-size: 25px;
            margin-right: 10px;
        }
        span{
            font-size: 16px;
        }
        &:hover{
            border-left:6px solid #427cff;
            background: linear-gradient(rgba(0,0,0,0.2), rgba(0,0,0,0.2));
        }
    }
}

.personal-sidebar .categories{
    margin:20px 0;
    p{
        font-size: 17px;
        color:#eee;
        margin:10px 10px 10px 29px;
    }
    a svg{
        font-size: 21px;
    }
    .add{
        background: linear-gradient(rgba(255,255,255,0.2), rgba(255,255,255,0.2));
        margin:0 25px;
        border:0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        border-radius: 25px;
        padding:9px 35px 9px 10px;
        color:#fff;
        font-size: 16px;
        margin-top: 20px;
        svg{
            font-size: 26px;
            margin-right: 15px;
        }
        &:hover{
            background: linear-gradient(rgba(255,255,255,0.1), rgba(255,255,255,0.1));
        }
    }
}

.personal-sidebar .teams{
    padding:20px 0;
    border-top: 1px solid #ccc;
    margin:40px 0 25px;
    .inline{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding:0 20px;
        p{
            font-size: 17px;
            color:#eee;
        }
        button{
            font-size: 23px;
            margin:0 5px;
            border:0;
            cursor: pointer;
            color:#eee;
            background: transparent;
            svg{
                margin-bottom:-4px;
            }
        }
    }
    .search-bar{
        border:1px solid #ccc;
        display: flex;
        align-items: center;
        margin:20px 15px 15px;
        padding:0 10px 0 15px;
        border-radius: 25px;
        button{
            font-size: 19px;
            margin-right: 4px;
            border:0;
            background: transparent;
            color:#eee;
            cursor: pointer;
            svg{
                margin-bottom: -4px;
            }
        }
        input{
            flex: 1;
            border-radius: 25px;
            border:0;
            padding:0 5px;
            font-size: 14px;
            height: 38px;
            background: transparent;
            color:#eee;
            &::placeholder{
                color:#eee;
            }
            &:focus{
                outline: none;
            }
        }
    }
    .team-item{
        margin:3px 0;
        display: flex;
        align-items: center;
        padding:8.5px 10px 8.5px 15px;
        &:hover{
            background: linear-gradient(rgba(0,0,0,0.2), rgba(0,0,0,0.2));
            .icon{
                background: linear-gradient(rgba(24, 46, 116, 0.5), rgba(24, 46, 116, 0.5));
            }
        }
    }
    .icon{
        width: 45px;
        height: 45px;
        border-radius: 50%;
        position: relative;
        background: linear-gradient(rgba(0,0,0,0.2), rgba(0,0,0,0.2));
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 22.5px;
        color:#fff;
    }
    .circle{
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background: rgb(79, 201, 79);
        position: absolute;
        right: 0px;
        bottom:4px;
    }
    .block{
        display: flex;
        flex-direction: column;
        flex: 1;
        margin:0 5px 0 10px;
        span{
            font-size: 15px;
            color:#f3f3f3;
            margin-bottom: 3px;
        }
        small{
            font-size: 13px;
            color:#ccc;
        }
    }
    .team-item button{
        font-size: 22px;
        border:0;
        background: transparent;
        cursor: pointer;
        color:#eee;
        svg{
            margin-bottom: -4px;
        }
    }
    .all{
        font-size: 17px;
        color:#fff;
        cursor: pointer;
        background: linear-gradient(rgba(255,255,255,0.3), rgba(255,255,255,0.3));
        border:0;
        padding:13px 25px 11px;
        border-radius: 25px;
        margin:10px 0 0 15px;
        &:hover{
            background: linear-gradient(rgba(255,255,255,0.15), rgba(255,255,255,0.15));
        }
    }
}

.personal-sidebar .premium{
    margin:10px 15px 50px;
    border-radius: 10px;
    background: #152c5f;
    color:#fff;
    padding:25px 15px;
    .img{
        content: url('../../assets/Business/img1.png');
        width: 45%;
    }
    h5{
        font-size: 17px;
        font-weight: normal;
        margin:15px 0;
    }
    .item{
        display: flex;
        align-items: center;
        margin:9.5px 0;
        svg{
            font-size: 18px;
            margin-right: 5px;
        }
        span{
            font-size: 14px;
        }
    }
    button{
        background: #427cff;
        color:#fff;
        border:0;
        cursor: pointer;
        padding:8px 25px;
        font-size: 16px;
        border-radius: 20px;
        margin-top: 15px;
    }
}

.personal-sidebar .appearance-row{
    border-top:1px solid #ccc;
    padding:20px 0;
    margin:20px 5px 75px;
    button{
        width: 50%;
        font-size: 15px;
        border:0;
        background: transparent;
        cursor: pointer;
        color:#eee;
    }
}

@media(max-width:1401px){
    .personal-sidebar{
        width:225px;
    }
    .personal-sidebar::-webkit-scrollbar {
        width: 5px;
    }
    .personal-sidebar .logo-container{
        margin:20px 15px;
        .logo{
            width: 25px;
            margin-right: 6px;
        }
        h6{
            font-size: 14px;
        }
        button{
            width: 28px;
            height: 28px;
            font-size: 25px;
        }
    }
    
    .personal-sidebar .link-cont{
        margin:1px 0;
        a{
            border-left:5px solid transparent;
            padding:8px 10px 8px 20px;
            svg{
                font-size: 22px;
                margin-right: 10px;
            }
            span{
                font-size: 13.5px;
            }
            &:hover{
                border-left:5px solid #427cff;
            }
        }
    }
    
    .personal-sidebar .categories{
        margin:17.5px 0;
        p{
            font-size: 14px;
            margin:10px 10px 10px 24px;
        }
        a svg{
            font-size:16px;
        }
        .add{
            margin:0 15px;
            border-radius: 20px;
            padding:7px 25px 7px 8px;
            font-size: 14px;
            margin-top: 15px;
            svg{
                font-size: 24px;
                margin-right: 12.5px;
            }
        }
    }
    
    .personal-sidebar .teams{
        padding:15px 0;
        margin:30px 0 20px;
        .inline{
            padding:0 15px;
            p{
                font-size: 14px;
            }
            button{
                font-size: 17px;
                margin:0 4px;
                svg{
                    margin-bottom:-3px;
                }
            }
        }
        .search-bar{
            margin:20px 10px 15px;
            padding:0 5px 0 12.5px;
            border-radius: 20px;
            button{
                font-size: 15px;
                margin-right: 3px;
                svg{
                    margin-bottom: -3px;
                }
            }
            input{
                border-radius: 20px;
                padding:0 3px;
                font-size: 12px;
                height: 33px;
            }
        }
        .team-item{
            margin:2.5px 0;
            padding:8px 7.5px 8px 12.5px;
        }
        .icon{
            width: 40px;
            height: 40px;
            font-size: 19.5px;
        }
        .circle{
            width: 8.5px;
            height: 8.5px;
        }
        .block{
            margin:0 4px 0 7.5px;
            span{
                font-size: 13px;
                margin-bottom: 3px;
            }
            small{
                font-size: 11px;
            }
        }
        .team-item button{
            font-size: 19px;
            svg{
                margin-bottom: -3px;
            }
        }
        .all{
            font-size: 14px;
            padding:10px 20px 9px;
            border-radius: 20px;
            margin:8px 0 0 15px;
        }
    }
    
    .personal-sidebar .premium{
        margin:10px 10px 40px;
        padding:20px 10px;
        h5{
            font-size: 14px;
            margin:13px 0;
        }
        .item{
            margin:8px 0;
            svg{
                font-size: 15px;
            }
            span{
                font-size: 12px;
            }
        }
        button{
            padding:7px 20px 8px;
            font-size: 14px;
            margin-top: 10px;
        }
    }
    
    .personal-sidebar .appearance-row{
        padding:15px 0;
        margin:15px 0px 65px;
        button{
            font-size: 12px;
        }
    }
}




.small-personal-sidebar{
    width:85px;
    ::-webkit-scrollbar {
        width: 3px;
    }    
    .logo-container{
        justify-content: center;
        a{
            display: none;
        }
        button{
            width: 42.5px;
            height: 42.5px;
            border-radius: 3px;
        }
    }
    
    .link-cont{
        a{
           justify-content: center;    
           padding-left:4px;
           padding-right: 10px;
           svg{
               margin:0;
           }      
            span{
                display: none;
            }
        }
    }
    
    .categories{
        p{
            font-size: 11px;
            text-align: center;
            margin:10px 0px 10px 0px;
        }
        .add{
            justify-content: center;
            padding-left: 0;
            padding-right: 0;
            background: transparent;
            margin-left: auto;
            margin-right: auto;
           
            svg{
                margin:0;
            }
            span{
                display: none;
            }
            &:hover{
                background: transparent;
            }
        }
    }
    
    .teams{
      display: none;
    }
    
    .premium{
      display: none;
    }
    
    .appearance-row{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        button{
            margin:7.5px 0;
            width: 100%;
            font-size: 13px;
        }
    }
}

@media(max-width:1401px){
    .small-personal-sidebar{
        width:70px;   
        .logo-container{
            button{
                width: 38.5px;
                height: 35px;
            }
        }

        .categories{
            p{
                font-size: 9px;
            }
        }
        .appearance-row{
            button{
                font-size: 10.5px;
            }
        }
    }
    
}

@media(max-width:930px){
    .personal-sidebar{
        display: none;   
    }

}

//mode container===============
.personal-sidebar .mode-container{
    position: absolute;
    bottom:0;
    left:0;
    right: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    background: linear-gradient(rgba(0,0,0,0.2), rgba(0,0,0,0.2));
    padding:14px 0;
    a{
        margin:0 20px;
        color:#fff;
        display: flex;
        align-items: center;
        font-size: 16.5px;
        svg{
            font-size: 20px;
            margin-right: 7.5px;
        }
    }

}


@media(max-width:1401px){
    .personal-sidebar .mode-container{
        a{
            font-size: 15px;
            svg{
                font-size: 17.5px;
            }
        }
    
    }
}

.small-personal-sidebar .mode-container{
    justify-content: center;
    a{
        display: none;
    }

}
//appearance bg---------------
.apperance-group-bg{
    position: fixed;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    background: linear-gradient(rgba(30, 39, 68, 0.5), rgba(30, 39, 68, 0.5));
    z-index: 1200;
}

body.dark-mode{
    .apperance-group-bg{     
        background: linear-gradient(rgba(12, 28, 62, 0.75), rgba(12, 28, 62, 0.75));
    }
}

