.personal-dashboard-page  .all-items-dashboard .tab-panel2 .category{
    margin:35px 0;
    .title{
        padding-bottom: 10px;
        margin-bottom: 30px;
        display: flex;
        align-items: center;
        color:#888;
        font-size: 18px;
        border-bottom: 1px solid #ccc;
        svg{
            font-size: 20px;
            margin-right: 5px;
        }
    }
    .item1{
        display: flex;
        align-items: center;
        justify-content: space-between;
        border:1px solid #ddd;
        border-radius: 10px;
        padding:15px 25px;
        margin:20px 0;
    }
    .check-cont{
        display: flex;
        align-items: center;
        input{
            transform: scale(1.7);
            margin-right: 25px;
        }
    }
    .img-cont{
        width: 200px;
        display: flex;
        justify-content: center;
        .img{
            height: 32.5px;
        }
    }
    .text{
        h6{
            font-size: 15px;
            margin-bottom: 3px;
        }
        .text-inline{
            display: flex;
            align-items: center;
            color:#777;
        }
        svg{
            font-size: 15px;
            margin-right: 5px;
        }
        p{
            font-size: 14px;
        }
    }
    .block2{
        display: flex;
        flex-direction: column;
        align-items: center;
        small{
            font-size: 14px;
            margin-bottom: 3px;
            color:#777;
        }
        span{
            font-size: 15.5px;
        }
    }

    .inline1{
        display: flex;
        align-items: center;
        .btn1{
            width: 35px;
            height: 32.5px;
            border:0;
            border-radius: 3px;
            font-size: 22px;
            margin:0 6px;
            cursor: pointer;
            svg{
                margin-bottom: -4px;
            }
        }
    }
    .edit{
        background: #f2f2f2;
        color:#888;
    }
    .delete{
        background: #F63B3B;
        color:#fff;
    }
    .launch{
        border:1px solid #427cff;
        border-radius: 5px;
        color:#427cff;
        background: transparent;
        font-size: 17px;
        padding:8px 15px;
        margin:0 7.5px;
        cursor: pointer;
        &:hover{
            background: #427cff;
            color:#fff;
        }
    }
}

@media(max-width:1401px){
    .personal-dashboard-page .all-items-dashboard .tab-panel2 .category{
        margin:30px 0;
        .title{
            margin-bottom: 25px;
            font-size: 15px;
            svg{
                font-size: 18px;
            }
        }
        .item1{
            padding:15px 20px;
            margin:15px 0;
        }
        .check-cont{
            input{
                transform: scale(1.5);
                margin-right: 15px;
            }
        }
        .img-cont{
            width: 140px;
            .img{
                height: 22.5px;
            }
        }
        .text{
            h6{
                font-size: 13.5px;
                margin-bottom: 2.5px;
            }
            svg{
                font-size: 14px;
                margin-right: 5px;
            }
            p{
                font-size: 12px;
            }
        }
        .block2{
            small{
                font-size: 12px;
                margin-bottom: 2.5px;
            }
            span{
                font-size: 13px;
            }
        }
    
        .inline1{
            .btn1{
                width: 31px;
                height: 30px;
                font-size: 20px;
                margin:0 5px;
                svg{
                    margin-bottom: -3px;
                }
            }
        }
        .launch{
            font-size: 14px;
            padding:7px 15px;
            margin:0 7.5px;
        }
    }
}

@media(max-width:1000px){
    .personal-dashboard-page  .all-items-dashboard .tab-panel2, .personal-dashboard-page .all-items-dashboard .react-tabs__tab-list .tab-btns{
        display: none;
    }
}

body.dark-mode{
    .personal-dashboard-page  .all-items-dashboard .tab-panel2 .category{
        .title{
            color:#ccc;
            border-bottom: 1px solid #ddd;
        }
        .item1{
            border:1px solid #091531;   
            background: #091531;  
        }
        .text{
            h6{
               color:#fff;
            }
            .text-inline{
                color:#ccc;
            }
        }
        .block2{
            small{
                color:#fff;
            }
            span{
               color:#999;
            }
        }
        .edit{
            background: #2C3E66;
            color:#fff;
        }
    }
    
}