.edit-password-form{
    position: fixed;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%);
    width: 40%;
    background: #fff;
    border:1px solid #eee;
    overflow: hidden;
    z-index: 2500;
    border-radius: 10px;
    .title{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding:15px 35px;
        background: #427cff;
        h4{
            font-size: 20px;
            color:#fff;
        }
        button{
            background: transparent;
            color:#fff;
            cursor: pointer;
            border: 0;
            font-size: 25px;
            svg{
                margin-bottom: -3px;
            }
        }
    }
    .inputs{
        padding:20px 35px;
    }
}
.edit-password-form .inputs .inner-row{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
    margin:15px 0;
    h6{
        width: 20%;
        font-size: 16px;
        color:#222;
    }
    input{
        width: 75%;
        height: 40px;
        padding:0 15px;
        border:1px solid #ccc;
        border-radius: 5px;
        font-size: 15px;
        color:#444;
        &:focus{
            outline: none;
        }
    }
    textarea{
        width: 75% !important;
        max-width: 75% !important;
        min-width: 75% !important;
        padding: 10px 15px;
        border:1px solid #ccc;
        border-radius: 5px;
        height: 80px !important;
        min-height: 80px !important;
        max-height: 80px !important;
        color:#444;
        font-family: 'Raleway', sans-serif;
        font-size: 15px;
        &:focus{
            outline: none;
        }
    }
    .css-1pahdxg-control,.css-2b097c-container, .css-yk16xz-control {
        box-shadow: none !important;
        border-color: hsl(0, 0%, 80%) ;
        width: fit-content;
        font-size: 15px;
    }
    .css-1pahdxg-control{
        width: 100%;        
    }
    .css-2b097c-container{
        width: 75%;
    }
    .css-yk16xz-control{
        width: 100%;

    }
    .css-1uccc91-singleValue {
        max-width: 100%;
    }
    .css-g1d714-ValueContainer{
        padding:0 8px;
        margin:-3.5px 0;
    }
}

.edit-password-form .inner-row2{
    padding:20px 0 0;
    margin:15px 0;
    border-top: 1px solid #ccc;
    h6{
        font-size: 16px;
        color:#222;
        margin-bottom: 15px;
    }
    .checkboxes{
        display: flex;
    }
    .check{
        display: flex;
        align-items: center;
        font-size: 15px;
        margin-right: 25px;
    }
    input{
        margin-right: 7.5px;
        transform: scale(1.2);
    }
}
.edit-password-form .inner-row3{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid #ccc;
    padding:22.5px 35px;
    .btns1, .btns2{
        display: flex;
        align-items: center;
        button:hover{
            box-shadow: rgba(0, 0, 0, 0.1) 0px -200px 36px -28px inset;
            transition: 0.1s ease-in-out;
        }
    }
    .btns1{
        button{
            width: 40px;
            height: 40px;
            border-radius: 5px;
            border: 0;
            font-size: 25px;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            &:nth-child(2){
                margin:0 10px;
            }
        }
    }
    .share{
        background-color: #f5f5f5;
        color:#555;
    }
    .delete{
        background-color: #F63B3B;
        color:#fff;
    }
    .btns2{
        button{
            font-size: 17px;
            padding:10px 20px;
            border-radius: 5px;
            cursor: pointer;
            &:first-child{
                margin-right:10px;
            }
        }
        .cancel{
            border:1px solid #ddd;
            background-color: #f5f5f5;
        }
        .save{
            border:1px solid #427cff;
            background-color: #427cff;
            color:#fff;
        }
    }
}

@media(max-width:1590px){
    .edit-password-form{
        width: 46%;
    }
}

@media(max-width:1401px){
    .edit-password-form{
        width: 50%;
        .title{
            padding:12px 25px;
            h4{
                font-size: 16px;
            }
            button{
                font-size: 21px;
            }
        }
        .inputs{
            padding:15px 25px;
        }
    }
    .edit-password-form .inputs .inner-row{
        margin:10px 0;
        h6{
            font-size: 13.5px;
        }
        input{
            height: 35px;
            padding:0 5px 0 15px;
            font-size: 12px;
        }
        textarea{
            padding: 7.5px 12.5px;
            height: 70px !important;
            min-height: 70px !important;
            max-height: 70px !important;
            font-size: 12px;
        }
        .css-1pahdxg-control,.css-2b097c-container, .css-yk16xz-control {
            font-size: 12px;
        }
        .css-g1d714-ValueContainer{
            margin:-4px 0;
        }
    }
    
    .edit-password-form .inner-row2{
        padding:20px 0 0;
        margin:10px 0 0;
        h6{
            font-size: 13.5px;
            margin-bottom: 12.5px;
        }
        .check{
            font-size: 13px;
            margin-right: 20px;
        }
        input{
            margin-right: 5px;
            transform: scale(1.1);
        }
    }
    .edit-password-form .inner-row3{
        padding:20px 25px;
        .btns1{
            button{
                width: 32.5px;
                height: 32.5px;
                font-size: 18px;
                &:nth-child(2){
                    margin:0 7.5px;
                }
            }
        }
        .btns2{
            button{
                font-size: 14px;
                padding:7.5px 15px;
                &:first-child{
                    margin-right:7.5px;
                }
            }
        }
    }
}

@media(max-width:1050px){
    .edit-password-form{
        width: 300px;
        max-width: 100%;
        height: 70vh;
        overflow-y: auto;
        .title{
            padding:10px 10px 0;
            flex-wrap: wrap;
            h4{
                margin:5px 10px;
            }
            button{
                margin:5px 10px;
                svg{
                    margin:0;
                }
            }
        }
        .inputs{
            padding:15px 20px;
        }
    }
    .edit-password-form .inputs .inner-row{
        margin:15px 0;
        display: block;
        h6{
            font-size: 13.5px;
            margin-bottom: 5px;
            width: 100%;
        }
        input{
            width: 100%;
        }
        textarea{
            width: 100% !important;
            max-width: 100% !important;
            min-width: 100% !important;

            height: 100px !important;
            min-height: 100px !important;
            max-height: 100px !important;
        }
        .css-2b097c-container{
            width: 100%;
        }
    }
    
    .edit-password-form .inner-row2{
        padding:20px 0 0;
        margin:10px 0 0;
        h6{
            font-size: 13.5px;
            margin-bottom: 7.5px;
        }
        .checkboxes{
            flex-direction: column;
        }
        .check{
            margin:5px 0;
        }
        input{
            transform: scale(1);
        }
    }
    .edit-password-form .inner-row3{
        padding:15px 20px;
        display: block;
        .btns1, .btns2{
            margin:10px 0;
            justify-content: center;
        }
    }
    .edit-password-form{
        &::-webkit-scrollbar {
            width: 5px;
        }
        &::-webkit-scrollbar-track {
            background: #e2e4e7;
            border-radius: 10px;
        }
        &::-webkit-scrollbar-thumb {
            background-color:  #a3a9c4;
            border-radius: 10px;
        }
    }
}