body.dark-mode{
    .account-settings-page .row1 .inline{
        h1{
           color:#fff;
        }
    }
    
    .account-settings-page .row1 .react-tabs{
        .react-tabs__tab{
            color:#ddd;
            svg{
                color:#ddd;
            }
            p{
                
            }
        }
        .react-tabs__tab--selected{
            color:#ddd;
            border-bottom: 3px solid #427cff;
            svg{
                color:#427cff;
            }
        }
        .p{
            color:#fff;
        }
    }
    
    .account-settings-page .account-setting-component .item1{
        h4{
            color:#eee;
            background: #2C3E66;
        }
    }
    
    .account-settings-page .account-setting-component .mini-item1{
        h5{
            color:#ddd;
        }
        .inputs{
            p{
                color:#ddd;
            }
        }
        input{
            border:1px solid #ddd;
            color:#333;
        }
        button{
            border:1px solid #ddd;
            color:#333;
            background: #e7e7e7;
        }
        .checkboxes .inner-row{
            label{
               color:#ddd;
            }
        }
    }
    
    .account-settings-page .switch-container{
        .slider {
            background-color: #fff;
        }
          
        .slider:before {
            background-color: #b1c9ff;
        }
          
        input:checked + .slider {
            background-color: #427cff;
        }
        .text{
            color:#ddd;
        }
    }
    
    .account-settings-page .advanced h1{
        color:#fff;
    }
}