.personal-sidebar .switch-mode{
    position: relative;
    margin:0 20px;
    .dark-icon{
        top:50%;
        left:50%;
        transform: translate(-50%, -50%);
        position: absolute;
        background-color: #fff;
        width: 28px;
        height: 28px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 18px;
        cursor: pointer;
        color:#2B4173;
        padding:0 0 1.5px 1.5px;
    }
    input{
        opacity: 0;
    }

    input:checked + .dark-icon{
        background: #253864;
        color:#f5e834;
    }
}

@media(max-width:1401px){
    .personal-sidebar .switch-mode{
        .dark-icon{
            width: 25px;
            height: 25px;
            font-size: 16px;
        }
    }
}

.small-personal-sidebar .switch-mode{
    margin:0 auto;
}