body.dark-mode{
    .personal-sidebar{
        background: linear-gradient(#172544,#293d61 75%);
        box-shadow: 1px 0px 5px rgba(0,0,0,0.3);
    }
    .personal-sidebar::-webkit-scrollbar-track {
        background: #c9d0db;  
    }
    .personal-sidebar::-webkit-scrollbar-thumb {
        background-color:  #c9d0db;
    }
    
    .personal-sidebar .logo-container{
        h6{
            color:#eee;
        }
        button{  
            background:#374874;
            color:#eee;
        }
    }
    
    .personal-sidebar .link-cont{
        a{
            color:#ddd;
            svg{
                color:#ccc;
            }
            &:hover{
                border-left:6px solid #2b50a7;
            }
        }
    }
    
    .personal-sidebar .categories{
        p{       
            color:#ddd;        
        }
        .add{
            background: linear-gradient(rgba(255,255,255,0.1), rgba(255,255,255,0.1));
            color:#eee;
            &:hover{
                background: linear-gradient(rgba(255,255,255,0.05), rgba(255,255,255,0.05));
            }
        }
    }
    
    .personal-sidebar .teams{
        border-top: 1px solid #aaa;
        .inline{
   
            p{
                color:#ddd;
            }
            button{
                svg{
                    color:#ccc; 
                }
            }
        }
        .search-bar{
            border:1px solid #aaa;
            button{
                color:#ccc;
            }
            input{
                color:#ddd;
                &::placeholder{
                    color:#ddd;
                }
            }
        }
        .team-item{
            &:hover{
                background: linear-gradient(rgba(0,0,0,0.2), rgba(0,0,0,0.2));
                .icon{
                    background: linear-gradient(rgba(24, 46, 116, 0.5), rgba(24, 46, 116, 0.5));
                }
            }
        }
        .icon{
            background: linear-gradient(rgba(0,0,0,0.2), rgba(0,0,0,0.2));
            color:#fff;
        }      
        .block{
            span{
                color:#e3e3e3;
            }
            small{
                color:#bbb;
            }
        }
        .team-item button{
            color:#bbb;
        }
        .all{
            color:#ddd;
            background: linear-gradient(rgba(255,255,255,0.15), rgba(255,255,255,0.15));
            &:hover{
                background: linear-gradient(rgba(255,255,255,0.075), rgba(255,255,255,0.075));
            }
        }
    }
    
    .personal-sidebar .premium{
        background: #152342;
        color:#eee;
        button{
            background: #315bbd;
        }
    }
    
    .personal-sidebar .appearance-row{
        border-top:1px solid #bbb;
        button{
            color:#ddd;
        }
    }

    .personal-sidebar .mode-container{
        background: linear-gradient(rgba(0,0,0,0.3), rgba(0,0,0,0.3));
        a{
            color:#ddd;
        }
        input:checked + .dark-icon{
            background: #314983;
        }
    
    }
}