
.personal-toolbar{
    position: sticky;
    width: 100%;
    top: 0;
    left:0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #fff;
    z-index: 2000;
    padding: 10px 25px;
    border-bottom: 1px solid #ccc;
}
.personal-toolbar .dashboard-menu{
    display: none;
}
.personal-toolbar .col1{
    display: flex;
    align-items: center;
    .title{
        display: flex;
        align-items: center;
        color:#555;
        svg{
            font-size: 25px;
            margin-right: 10px;
        }
        p{
            font-size: 16px;
        }
    }
    .search-container{
        border:1px solid #ccc;
        display: flex;
        align-items: center;
        overflow: hidden;
        border-radius: 25px;
        margin:0 10px 0 20px;
        select{
            background-color: #e8e8e8;
            height: 40px;
            padding:0 5px 0 20px;
            border:0;
            font-size: 16px;
            color:#555;
            &:focus, &:active{
                outline: none;
            }
        }
        .search-bar{
            display: flex;
            align-items: center;
            padding:0 10px;
            input{
                height: 40px;
                padding:0 5px;
                border:0;
                font-size: 16px;
                color:#555;
                &:focus, &:active{
                    outline: none;
                }
            }
        }
        button{
            font-size: 20px;
            border:0;
            background: transparent;
            color:#888;
            margin-right: 5px;
            svg{
                margin-bottom: -3px;
            }
        }
    }
}

.personal-toolbar .col2{
    display: flex;
    align-items: center;
    .buttons{
        display: flex;
        align-items: center;
        .button{
            color:#999;
            border:0;
            cursor: pointer;
            background: transparent;
            margin:0 7.5px;
            font-size: 22.5px;
            svg{
                margin-bottom: -4px;
            }
        }
    }
    .upgrade{
        padding:10px 25px;
        border:1px solid #427cff;
        color:#427cff;
        cursor: pointer;
        font-size: 16px;
        border-radius: 20px;
        background: #f1f5ff;
        margin:0 10px 0 0;
    }
}



.personal-toolbar .col2 .user{
    display: flex;
    align-items: center;
    margin-left: 15px;
    padding-left: 15px;
    border-left: 1px solid #ccc;
    position: relative;
    .block{
        h6{
            font-size: 15px;
            margin-bottom: 1px;
        }
        p{
            font-size: 13.5px;
            color:#555;
        }
    }
    .inline{
        display: flex;
        align-items: center;
        margin-left: 10px;
        svg{
            font-size: 18px;
        }
    }
    .profile{
        content: url('../assets/personal/profile.png');
        width: 50px;
        margin-right: 2px;
    }
   
}

@media(max-width:1401px){
    .personal-toolbar{
        padding: 8px 20px;
    }
    .personal-toolbar .col1{
        .title{
            svg{
                font-size: 18px;
                margin-right: 5px;
            }
            p{
                font-size: 12px;
            }
        }
        .search-container{
            border-radius: 20px;
            margin:0 5px 0 10px;
            select{
                height: 30px;
                padding:0 5px 0 12px;
                font-size: 12px;
            }
            .search-bar{
                padding:0 7.5px;
                input{
                    height: 30px;
                    padding:0 5px;
                    font-size: 12px;
                }
            }
            button{
                font-size: 16px;
                margin-right: 1px;
            }
        }
    }
    
    .personal-toolbar .col2{
        .buttons{
            .button{
                margin:0 7px;
                font-size: 16px;
                svg{
                    margin-bottom: -3px;
                }
            }
        }
        .upgrade{
            padding:7px 17.5px;
            font-size: 13px;
            border-radius: 20px;
            margin:0 7.5px 0 0;
        }
    }
    
    
    
    .personal-toolbar .col2 .user{
        margin-left: 10px;
        padding-left: 10px;
        .block{
            h6{
                font-size: 13px;
                margin-bottom: 1px;
            }
            p{
                font-size: 11px;
            }
        }
        .inline{
            margin-left: 10px;
            svg{
                font-size: 15px;
            }
        }
        .profile{
            width: 40px;
            margin-right: 1px;
        }
       
    }
}

@media(max-width:1100px){
    .personal-toolbar{
        padding:10px 25px;
    }
    .personal-toolbar .col1{
        display: none;
    }
    .personal-toolbar .col2 .buttons{
        display: none;
    }
    .personal-toolbar .col2 .user{
       margin:0;
       padding:0;
       border:0;   
    }
    .personal-toolbar .dashboard-menu{
        display: block;
        color:#2B4173;
        background: none;
        font-size: 35px;
        margin-right: 15px;
        border:0;
        cursor: pointer;
        svg{
            margin-bottom: -4px;
        }
    }
}

@media(max-width:475px){
    .personal-toolbar .col2 .user .block{
        display: none;
    }   
}

body.dark-mode{
    .personal-toolbar{
        background: #091531;
        border-bottom: 1px solid #091531;
    }
    .personal-toolbar .col1{
        .title{
            color:#ccc;
        }
        .search-container{
            border:1px solid #aaa;
            select{
                background-color: #2C3E66;
                color:#ccc;
            }
            .search-bar{
                input{
                    background: transparent;
                    color:#ccc;
                    &::placeholder{
                        color:#ccc;
                    }
                }
            }
            button{
                color:#ccc;
            }
        }
    }

    .personal-toolbar .col2{
        .buttons{
            .button{
                color:#ccc;
            }
        }
        .upgrade{
            background: transparent;
            border:1px solid #fff;
            color:#fff;
        }
    }
    
    .personal-toolbar .col2 .user{
    
        border-left: 1px solid #aaa;
        .block{
            h6, p{
                color:#ccc;
            }
        }        
        .inline svg{
            color:#ccc;
        }
    }
}

//===============================================
.personal-toolbar .logout-container{
    background-color: #fff;
    position: absolute;
    right: 0;
    top:55px;
    width: 275px;
    border:1px solid #eee;
    border-radius: 5px;
    padding: 15px;
    .inline{
        display: flex;
        align-items: center;
        margin-bottom: 25px;
    }
    .profile2{
        content: url('../assets/personal/profile.png');
        width: 52.5px;
        margin-right: 10px;
    }
    h6{
        font-size: 15px;
    }
    p{
        font-size: 14px;
        margin:2px 0 4px;
        color:#555;
    }
    .block2 span{
        color:#fff;
        background: #222;
        padding:6px 27.5px;
        font-size: 12px;
        border-radius: 12.5px;
    }
    .inline2{
        margin:7.5px 0;
        a{
            padding:7.5px 10px;
            border-radius: 4px;
            display: flex;
            align-items: center;
            font-size: 15.5px;
            color:#000;
            svg{
                color:#427cff;
                font-size: 18px;
                margin-right: 12.5px;
            }
            &:hover{
                background: #eff4ff;
            }
        }
    }

    button{
        background: #427cff;
        color:#fff;
        display: flex;
        align-items: center;
        cursor: pointer;
        border: 0;
        border-radius: 20px;
        font-size: 15px;
        padding:8.5px 20px;
        margin-top: 20px;
        svg{
            margin-right: 10px;
            font-size: 18px;
        }
        &:hover{
            background: #3866d1;
        }
    }
}


@media(max-width:1401px){
    .personal-toolbar .logout-container{
        top:45px;
        width: 190px;
        padding:12.5px 7.5px;
        .inline{
            margin-bottom: 15px;
        }
        .profile2{
            width: 37.5px;
            margin-right: 7.5px;
        }
        h6{
            font-size: 12px;
        }
        p{
            font-size: 11px;
            margin:3px 0;
        }
        .block2 span{
            padding:4px 20px;
            font-size: 11px;
        }
        .inline2{
            margin:7.5px 0;
            a{
                padding:4px 7.5px;
                font-size: 13px;
            }
            a svg{
                font-size: 15px;
            }
        }
    
        button{
            font-size: 12px;
            padding:6px 15px;
            margin: 15px 7.5px 5px;
            svg{
                font-size: 15px;
            }
        }
    }
}
@media(max-width:500px){
    .personal-toolbar .logout-container{
        .inline2{
            margin:5px 0;
        }
    }
}


body.dark-mode{
    .personal-toolbar .logout-container{
        background-color: #2C3E66;
        border:1px solid #2C3E66;
        h6{
            color:#eee;
        }
        p{
            color:#bbb;
        }
        .block2 span{
            color:#fff;
            background: #1a253b;
        }
        .inline2{
            a{
                color:#eee;
                svg{
                    color:#a6c1ff;
                }
                &:hover{
                    background: #1a253b;
                }
            }
        }
    
        button{
            background: #427cff;
            color:#fff;
        }
    }
    .personal-toolbar .col2 .user{
        border-left: 1px solid transparent;
    }
}


//appearance bg---------------
.apperance-group-bg{
    position: fixed;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    background: linear-gradient(rgba(30, 39, 68, 0.5), rgba(30, 39, 68, 0.5));
    z-index: 1200;
}

body.dark-mode{
    .apperance-group-bg{     
        background: linear-gradient(rgba(12, 28, 62, 0.75), rgba(12, 28, 62, 0.75));
    }
}
