.security-dashboard-page .all-items-dashboard .right{
    background: #f8f8f8;
    flex: 1;
    padding:35px 20px;
    border-left:1px solid #ddd;
    border-bottom:1px solid #ddd;
    border-radius: 5px;
    .alerts{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 30px;
        button{
            font-size: 17px;
            background: none;
            border:0;
            color:#444;
            text-decoration: underline;
            cursor: pointer;
        }
    }
    .inline{
        display: flex;
        align-items: center;
        h5{
            font-size: 22.5px;
            margin-right: 10px;
        }
        span{
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: #427cff;
            color:#fff;
            width: 30px;
            height: 30px;
            border-radius: 50%;
            font-size: 20px;
            padding-bottom: 4px;
        }
    }
    .dark-web{
        background: #fff;
        border-radius: 10px;
        padding:20px;
    }
    .inline2{
        display: flex;
        align-items: center;
        margin-bottom: 25px;
    }
    .logo{
        content: url('../../assets/PersonalSecurity/logo.png');
        width: 55px;
        margin-right: 10px;
    }
    .block{
        flex: 1;
        h6{
            font-size: 16px;
            margin-bottom: 5px;
        }
        small{
            font-size: 15px;
        }
    }
    .item3{
        margin:20px 0;
        span{
            font-size: 16px;
            color:#427cff;
            margin-bottom: 7.5px;
        }
        p{
            font-size: 16px;
            padding-top: 5px;
        }
    }
    .text{
        font-size: 16px;
        margin:10px 0 25px;
    }
    .link-cont{
        display: flex;
        justify-content: flex-end;
    }
    .link-cont a{
        background: #427cff;
        color:#fff;
        font-size: 17px;
        padding:11px 22.5px;
        border-radius: 22px;
    }
}

@media(max-width:1401px){
    .security-dashboard-page .all-items-dashboard .right{
        padding:30px 15px;
        .alerts{
            margin-bottom: 25px;
            button{
                font-size: 15px;
            }
        }
        .inline{
            h5{
                font-size: 17.5px;
            }
            span{
                width: 25px;
                height: 25px;
                font-size: 17.5px;
                padding-bottom: 3px;
            }
        }
        .dark-web{
            padding:15px;
        }
        .inline2{
            margin-bottom: 20px;
        }
        .logo{
            width: 40px;
        }
        .block{
            h6{
                font-size: 14px;
                margin-bottom: 5px;
            }
            small{
                font-size: 13px;
            }
        }
        .item3{
            margin:15px 0;
            span{
                font-size: 14px;
            }
            p{
                font-size: 14px;
            }
        }
        .text{
            font-size: 14px;
            margin:10px 0 25px;
        }
        .link-cont a{
            font-size: 15px;
            padding:10px 20px;
            border-radius: 22px;
        }
    }
}

@media(max-width:815px){
    .security-dashboard-page .all-items-dashboard .right{
        border:0;
    }
}

body.dark-mode{
    .security-dashboard-page .all-items-dashboard .right{
        background: #2C3E66;
        border-left:1px solid #2C3E66;
        border-bottom:1px solid #2C3E66;

        .alerts{
            button{
                color:#ccc;
            }
        }
        .inline{
            h5{
                color:#fff;
            }
        }
        .dark-web{
            background: #091531;
        }
        .block{
            h6, small{
               color:#fff; 
            }
        }
        .item3{
            span{
                color:#fff;
            }
            p{
                color:#bbb;
            }
        }
        .text{
            color:#bbb;
        }       
    }
}