.personal-sidebar .appearance-group{
    background-color: #fff;
    border:1px solid #ddd;
    position: fixed;
    left: 50%;
    top:50%;
    transform: translate(-50%, -50%);
    z-index: 2000 !important;
    padding:25px;
    border-radius: 15px;
    width: 50%;
    h1{
        font-size: 25px;
    }
    .p{
        font-size: 17px;
        margin:10px 0;
    }
    .category{
        margin:30px 0;
    }
    .category-title{
        font-size: 17px;
    }
    .options{
        display: flex;
        justify-content: space-between;
        margin-top: 10px;
    }
    .option{
        border:1px solid #ccc;
        padding:10px;
        width: 30%;
        border-radius: 6px;
        .img{
            width: 100%;
        }
    }
    .inline{
        display: flex;
        align-items: center;
        margin-bottom: 15px;
        input{
            transform: scale(1.35);
            margin-right: 10px;
        }
        .name{
            font-size: 17px;
        }
    }
    .btns{
        display: flex;
        flex-wrap: wrap;
        button{
            border:0;
            font-size: 17px;
            font-weight: bold;
            margin:5px 10px 5px 0;
            border-radius: 5px;
            padding:12.5px 17.5px;
            cursor: pointer;
        }
        .btn1{
            background: none;
        }
        .btn2{
            background: #427cff;
            color:#fff;
        }
    }
}

@media(max-width:1401px){
    .personal-sidebar .appearance-group{
        padding:20px;
        border-radius: 10px;
        h1{
            font-size: 20px;
        }
        .p{
            font-size: 15px;
        }
        .category{
            margin:20px 0;
        }
        .category-title{
            font-size: 15px;
        }
        .options{
            flex-wrap: wrap;
            margin-top: 8px;
        }
        .option{
            width: 31%;
        }
        .inline{
            margin-bottom: 15px;
            input{
                transform: scale(1.25);
                margin-right: 10px;
            }
            .name{
                font-size: 15px;
            }
        }
        .btns{
            button{
                font-size: 14px;
                margin:3px 10px 3px 0;
                padding:10px 17.5px;
            }
        }
    }
}

@media(max-width:991px){
    .personal-sidebar .appearance-group{
        width: 75%;
    }
}


@media(max-width:767px){
    .personal-sidebar .appearance-group{
        width: 250px;
        max-width: 90%;
        height: 75vh;
        overflow-y: auto;
        border-radius: 7.5px;
        .option{
            width: 100%;
            margin:10px 0;
        }
        .options{
            margin-top: 0px;
        }
        &::-webkit-scrollbar {
            width: 7px;
        }
        &::-webkit-scrollbar-track {
            background: #e2e4e7;
            border-radius: 15px;
        }
        &::-webkit-scrollbar-thumb {
            background-color:  #a3a9c4;
            border-radius: 15px;
        }
        .btns{
            button{
                margin:3px 0;
            }
            .btn1{
                padding: 10px 0;
            }
        }
    }
}

body.dark-mode{
    .personal-sidebar .appearance-group{
        background-color: #091531;
        border:1px solid #091531;
        h1, .p, .category-title{
           color:#fff;            
        }
        .option{
            border:1px solid #ddd;
        }
        .inline{
            .name{
                color:#fff;  
            }
        }
        .btns{
            .btn1{
                color:#fff;
            }
        }
    }
}