.sharing-center-page .all-items-dashboard {
    padding:3%;
    .row1{
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        h1{
            font-size: 35px;
            margin:10px 20px 10px 0;
        }
    }
    .right{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
    }
    .add{
        padding:12.5px 20px;
        border-radius: 28px;
        display: flex;
        align-items: center;
        font-size: 17px;
        border: 0;
        background: #427cff;
        color:#fff;
        cursor: pointer;
        margin:10px 22px 10px 0;
        svg{
            font-size: 23px;
            margin-right: 20px;
        }
    }
    .select-input{
        display: flex;
        align-items: center;
        border:1px solid #ccc;
        background: #fff;
        border-radius: 5px;
        padding-left: 10px;
        padding-top: 4px;
        padding-bottom:4px;
        margin:10px 0;

    }
    .select-input .icon{
        font-size: 22.5px;
        color: #555;
    }
    .css-1pahdxg-control,.css-2b097c-container, .css-yk16xz-control {
        box-shadow: none !important;
        border-color: hsl(0, 0%, 100%) ;
        width: fit-content;
        font-size: 17px;
    }
    .css-1pahdxg-control{
        width: 100%;
    }
    .css-2b097c-container{
        width: 175px;
    }
    .css-yk16xz-control{
        width: 100%;
    }
    .css-tlfecz-indicatorContainer,
    .css-1gtu0rj-indicatorContainer{
        margin:0 -5px !important;
    }
    

    .deleted-folders{
        margin:5px 3px 30px;
        color:#555;
        font-size: 17px;
        input{
            transform: scale(1.5);
            margin-right: 15px;
        }
    }
}

.sharing-center-page .react-tabs__tab-list{
    display: flex;
    border-bottom: 1px solid #ccc;
    margin:15px 0;
    .react-tabs__tab{
        display: flex;
        align-items: center;
        color:#555;
        font-size: 17px;
        margin-right:45px;
        margin-bottom: -1.5px;
        border-bottom: 2.5px solid transparent;
        padding:10px 5px;
        cursor: pointer;
        svg{
            font-size: 20px;
            margin-right: 10px;
            color:#777;
        }
        p{
            flex: 1;
        }
    }
    .react-tabs__tab--selected{
        border-bottom: 2.5px solid #427cff;
        svg{
            color:#427cff;
        }
    }
}
.sharing-center-page .tab-panel{
    // border:1px solid red;
    .category{
        // border:1px solid green;
        margin:45px 0;
    }
    .title{
        display: flex;
        align-items: center;
        p{
            color:#555;
            font-size: 17px;
            margin-right: 10px;
        }
        span{
            width: 22px;
            height: 22px;
            font-size: 16px;
            border-radius: 50%;
            background: #427cff;
            color:#fff;
            display: flex;
            align-items: center;
            justify-content: center;
            padding-bottom: 3px;
        }
    }
    .item1{
        border:1px solid #ddd;
        border-radius: 10px;
        padding:10px 30px;
        margin:25px 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .check-cont{
        display: flex;
        align-items: center;
        input{
            transform: scale(1.5);
            margin-right: 50px;
        }
        svg{
            color:#427cff;
            font-size: 50px;
        }
    }
    .item1 h6{
        font-size: 18px;
    }
    .block2{
        display: flex;
        flex-direction: column;
        align-items: center;
        small{
            font-size: 14px;
            margin-bottom: 3px;
            color:#777;
        }
        span{
            font-size: 15.5px;
        }
    }

    .inline1{
        display: flex;
        align-items: center;
        .btn1{
            width: 35px;
            height: 32.5px;
            border:0;
            border-radius: 3px;
            font-size: 22px;
            margin:0 6px;
            cursor: pointer;
            svg{
                margin-bottom: -4px;
            }
        }
    }
    .edit{
        background: #f2f2f2;
        color:#888;
    }
    .delete{
        background: #F63B3B;
        color:#fff;
    }
    .launch{
        border:1px solid #427cff;
        border-radius: 5px;
        color:#427cff;
        background: transparent;
        font-size: 17px;
        padding:9.5px 35px 8px;
        margin:0 7.5px;
        cursor: pointer;
        &:hover{
            background: #427cff;
            color:#fff;
        }
    }
}

@media(max-width:1401px){
    .sharing-center-page .all-items-dashboard {
        padding:4.5%;
        .row1{
            h1{
                font-size: 27px;
                margin:7.5px 15px 7.5px 0;
            }
        }        
        .add{
            padding:11px 17.5px;
            border-radius: 24px;
            font-size: 14px;
            margin:7.5px 15px 7.5px 0;
            svg{
                font-size: 20px;
                margin-right: 7.5px;
            }
        }
        .select-input{
            padding-left: 10px;
            margin:7.5px 0;
            padding-top: 1px;
            padding-bottom: 1px;
        }
        .select-input .icon{
            font-size: 20px;
        }
        .css-1pahdxg-control,.css-2b097c-container, .css-yk16xz-control {
            font-size: 14px;
        }
        .css-2b097c-container{
            width: 135px;
        }

       
        .row .icon{
            font-size: 22.5px;
        }
    
        .deleted-folders{
            margin:5px 2px 25px;
            font-size: 15px;
            input{
                transform: scale(1.3);
                margin-right: 12px;
            }
        }
    }
    
    .sharing-center-page .react-tabs__tab-list{
        margin:15px 0;
        .react-tabs__tab{
            font-size: 15px;
            margin-right:35px;
            svg{
                font-size: 18px;
                margin-right: 8px;
            }
        }
    }
    .sharing-center-page .tab-panel{
        .category{
            margin:40px 0;
        }
        .title{
            p{
                font-size: 15px;
            }
            span{
                width: 19px;
                height: 19px;
                font-size: 14px;
                padding-bottom: 1.5px;
            }
        }
        .item1{
            padding:10px 25px;
            margin:20px 0;
        }
        .check-cont{
            input{
                margin-right: 40px;
                transform: scale(1.3);
            }
            svg{
                font-size: 40px;
            }
        }
        .item1 h6{
            font-size: 15px;
        }
        .block2{
            small{
                font-size: 12px;
                margin-bottom: 3px;
            }
            span{
                font-size: 13.5px;
            }
        }
    
        .inline1{
            .btn1{
                width: 31.5px;
                height: 30px;
                font-size: 20px;
                margin:0 5px;
                svg{
                    margin-bottom: -3px;
                }
            }
        }
        .launch{
            font-size: 15px;
            padding:9px 30px 7px;
            margin:0 6px;
        }
    }
}

@media(max-width:925px){
    .sharing-center-page .tab-panel{
        .item1{
            flex-direction: column;
            justify-content: center;
            padding:30px;
        }
        .check-cont{
            input{
                margin-right: 20px;
            }
        }
        .item1 h6{
            margin:10px 0 17.5px;
        }
        .block2{
            margin-bottom: 25px;
            small{
                margin-bottom: 4px;
            }
        }
    
        .inline1{
            .btn1{
                width: 31.5px;
                height: 30px;
                font-size: 20px;
                margin:0 5px;
                svg{
                    margin-bottom: -3px;
                }
            }
        }
        .launch{
            font-size: 15px;
            padding:9px 30px 7px;
            margin:0 6px;
        }
    }


    .sharing-center-page .all-items-dashboard {
        padding:35px 5%;
        .row1{
            h1{
                font-size: 20px;
                text-align: center;
                margin:10px 0;
                width: 100%;
            }
        }        
        .right{
            justify-content: center;
        }
    
    
        .deleted-folders{
            display: flex;
           justify-content: center;
        }
    }
    
    .sharing-center-page .react-tabs__tab-list{
        margin:15px 0;
        flex-wrap: wrap;
        justify-content: center;
        .react-tabs__tab{
           width: fit-content;
           margin:1px 15px;
           border:0 !important;
        }
        .react-tabs__tab--selected{
            p{
                color:#427cff;
            }
        }
    }
}


