.personal-dashboard-page .all-items-dashboard {
    padding:3%;
    .row1{
        display: flex;
        align-items: center;
        border:1px solid #ccc;
        border-radius: 7px;
        padding:15px 25px;
        .img{
            content: url('../../assets/personal/icon.png');
            width: 50px;
            margin-right: 20px;
        }
        h6{
            font-size: 18px;
            margin-bottom: 5px;
        }
        p{
            color:#555;
            font-size: 16.5px;
        }
    }
}

.personal-dashboard-page .all-items-dashboard  .react-tabs__tab-list{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin:35px 0 10px;
    h1{
        font-size: 30px;
    }
    .tab-cont{
        display: flex;
        align-items: center;
    }
    .add{
        padding:13.5px 20px;
        border-radius: 28px;
        display: flex;
        align-items: center;
        font-size: 17px;
        border: 0;
        background: #427cff;
        color:#fff;
        cursor: pointer;
        svg{
            font-size: 23px;
            margin-right: 10px;
        }
    }
    .select-input{
        display: flex;
        align-items: center;
        border:1px solid #ccc;
        background: #fff;
        border-radius: 5px;
        padding-left: 10px;
        margin:0 15px;
        padding-top: 5px;
        padding-bottom: 5px;
    }
    .icon{
        font-size: 22.5px;
        color: #555;
    }
    .css-1pahdxg-control,.css-2b097c-container, .css-yk16xz-control {
        box-shadow: none !important;
        border-color: hsl(0, 0%, 100%) ;
        width: fit-content;
        font-size: 17px;
    }
    .css-1pahdxg-control{
        width: 100%;
    }
    .css-2b097c-container{
        width: 150px;
    }
    .css-yk16xz-control{
        width: 100%;
    }
    .css-tlfecz-indicatorContainer,
    .css-1gtu0rj-indicatorContainer{
        margin:0 -5px !important;
    }

    .tab-btns{
        display: flex;
        border: 1px solid #e9e9e9;
        background: #f2f2f2;
        padding:5px 3px;
        border-radius: 5px;
    }
    .react-tabs__tab{
        font-size: 30px;
        height: 40px;
        width: 40px;
        margin:0 3px;
        border-radius: 3px;
        background: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        color:#555;
        cursor: pointer;
    }
    .react-tabs__tab--selected{
        color:#427cff;
    }
}

.personal-dashboard-page .all-items-dashboard .tab-panel1 .category{
    margin:35px 0;
    // border:1px solid red;
    .title{
        padding-bottom: 10px;
        margin-bottom: 10px;
        display: flex;
        align-items: center;
        color:#888;
        font-size: 18px;
        border-bottom: 1px solid #ccc;
        svg{
            font-size: 20px;
            margin-right: 5px;
        }
    }
    .items1{
        display: flex;
        flex-wrap: wrap;
    }
    .item1{
        width: 18.5%;
        border:1px solid #ddd;
        margin:20px 1.5% 20px 0;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        overflow: hidden;
        position: relative;
        &:hover .top-div{
            display: flex;
        }
    }
    .img-cont{
        padding:30px 10px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .img{
        height: 35px;
    }
    .text{
        background: #3A4758;
        color:#fff;
        flex: 1;
        padding:15px;
        h6{
            font-size: 16px;
            margin-bottom: 7.5px;
        }
        .text-inline{
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            svg{
                font-size: 16px;
                margin-right: 5px;
            }
            p{
                font-size: 14px;
            }
        }
    }
    .top-div{
        -webkit-animation-name: top-div;
        animation-name: top-div;
        -webkit-animation-duration: 0.2s;
        animation-duration: 0.2s;
        -webkit-animation-timing-function: linear;
        animation-timing-function: linear;
        position: absolute;
        top:0;
        left:0;
        width: 100%;
        height: 100%;
        padding:10px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        background: #3A4758;
        display: none;
        transition: 0.2s ease-in-out;
        .inline1{
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: space-between;
            button{
                border:0;
                color:#fff;
                font-size: 20px;
                width: 33px;
                height: 30px;
                border-radius: 3px;
                margin:3.5px 4px;
                cursor: pointer;
                svg{
                    margin-bottom: -3px;
                }
            }
        }
        h5{
            color:#fff;
            font-size: 16px;
            margin:3.5px;
        }
        .edit{
            background: linear-gradient(rgba(255,255,255,0.2), rgba(255,255,255,0.2));
        }
        .delete{
            background: #F63B3B;
        }
        .launch{
            border:1px solid #fff;
            border-radius: 5px;
            color:#fff;
            background: transparent;
            font-size: 17px;
            padding:8px;
            margin:3.5px;
            cursor: pointer;
            &:hover{
                background: #fff;
                color:#3A4758;
            }
        }
    }
}



@-webkit-keyframes top-div {
    0% {
       -webkit-transform: translateY(100%);
       transform: translateY(100%);
     }
     100% {
         -webkit-transform: translateY(0%);
         transform: translateY(0%);
     }
 }
 @keyframes  top-div{
     0% {
         -webkit-transform: translateY(100%);
         transform: translateY(100%);
       }
       100% {
           -webkit-transform: translateY(0%);
           transform: translateY(0%);
       }
}

