.mobile-personal-sidebar{
    background: linear-gradient(#2B4173,#48629e 80%);
    position: fixed;
    top:0;
    left:0;
    width:225px;
    max-width:77.5%;
    z-index:2000;
    overflow-y: auto !important;
    box-shadow: 1px 0px 5px rgba(0,0,0,0.3);
    min-height: 100vh;
    height: 100%;
    transition: 0.15s ease-in-out;
    display: none;

    -webkit-animation-name: mobile-nav-scroll;
    animation-name: mobile-nav-scroll;
    -webkit-animation-duration: 0.2s;
    animation-duration: 0.2s;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;

}
.mobile-personal-sidebar::-webkit-scrollbar {
    width: 5px;
}
.mobile-personal-sidebar::-webkit-scrollbar-track {
    background: #e2e4e7;
    border-radius: 15px;
}
.mobile-personal-sidebar::-webkit-scrollbar-thumb {
    background-color:  #a3a9c4;
    border-radius: 15px;
}

.mobile-personal-sidebar .logo-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin:20px 15px;
    a{
        display: flex;
        align-items: center;
    }
    .logo{
        content: url('../../assets/Navbar/blue-logo.png');
        width: 25px;
        margin-right: 6px;
    }
    h6{
        color:#fff;
        font-size: 14px;
    }
    button{
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 0;
        cursor: pointer;
        background:#4a629e;
        color:#fff;
        width: 28px;
        height: 28px;
        font-size: 20px;
    }
}

.mobile-personal-sidebar .link-cont{
    margin:1px 0;
    a{
        display: flex;
        align-items: center;
        color:#eee;
        border-left:5px solid transparent;
        padding:8px 10px 8px 20px;
        svg{
            font-size: 22px;
            margin-right: 10px;
        }
        span{
            font-size: 13.5px;
        }
        &:hover{
            border-left:5px solid #427cff;
            background: linear-gradient(rgba(0,0,0,0.2), rgba(0,0,0,0.2));
        }
    }
}

.mobile-personal-sidebar .categories{
    margin:17.5px 0;
    p{
        color:#eee;
        font-size: 14px;
        margin:10px 10px 10px 24px;
    }
    a svg{
        font-size:16px;
    }
    .add{
        background: linear-gradient(rgba(255,255,255,0.2), rgba(255,255,255,0.2));
        border:0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        color:#fff;
        margin:0 15px;
        border-radius: 20px;
        padding:7px 25px 7px 8px;
        font-size: 14px;
        margin-top: 15px;
        svg{
            font-size: 24px;
            margin-right: 12.5px;
        }
        &:hover{
            background: linear-gradient(rgba(255,255,255,0.1), rgba(255,255,255,0.1));
        }
    }
}

.mobile-personal-sidebar .teams{
    padding:15px 0;
    margin:30px 0 20px;
    border-top: 1px solid #ccc;
    .inline{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding:0 15px;
        p{
            font-size: 14px;
            color:#eee;
        }
        button{
            border:0;
            cursor: pointer;
            color:#eee;
            background: transparent;
            font-size: 17px;
             margin:0 4px;
            svg{
                margin-bottom:-3px;
            }
        }
    }
    .search-bar{
        border:1px solid #ccc;
        display: flex;
        align-items: center;
        margin:20px 10px 15px;
        padding:0 5px 0 12.5px;
        border-radius: 20px;
        button{
            border:0;
            background: transparent;
            color:#eee;
            cursor: pointer;
            font-size: 15px;
            margin-right: 3px;
            
            svg{
                margin-bottom: -4px;
            }
        }
        input{
            flex: 1;
            border:0;
            padding:0 5px;
            background: transparent;
            color:#eee;
            border-radius: 20px;
            padding:0 3px;
            font-size: 12px;
            height: 33px;
            &::placeholder{
                color:#eee;
            }
            &:focus{
                outline: none;
            }
        }
    }
    .team-item{
        display: flex;
        align-items: center;
        margin:2.5px 0;
        padding:8px 7.5px 8px 12.5px;
        &:hover{
            background: linear-gradient(rgba(0,0,0,0.2), rgba(0,0,0,0.2));
            .icon{
                background: linear-gradient(rgba(24, 46, 116, 0.5), rgba(24, 46, 116, 0.5));
            }
        }
    }
    .icon{
        border-radius: 50%;
        position: relative;
        background: linear-gradient(rgba(0,0,0,0.2), rgba(0,0,0,0.2));
        display: flex;
        align-items: center;
        justify-content: center;
        color:#fff;
        width: 40px;
        height: 40px;
        font-size: 19.5px;
    }
    .circle{
        width: 8.5px;
        height: 8.5px;
        border-radius: 50%;
        background: rgb(79, 201, 79);
        position: absolute;
        right: 0px;
        bottom:4px;
    }
    .block{
        display: flex;
        flex-direction: column;
        flex: 1;
        margin:0 4px 0 7.5px;
        span{
            color:#f3f3f3;
            font-size: 13px;
            margin-bottom: 3px;
        }
        small{
            font-size: 11px;
            color:#ccc;
        }
    }
    .team-item button{
        font-size: 19px;
        border:0;
        background: transparent;
        cursor: pointer;
        color:#eee;
        svg{
            margin-bottom: -3px;
        }
    }
    .all{
        color:#fff;
        cursor: pointer;
        background: linear-gradient(rgba(255,255,255,0.3), rgba(255,255,255,0.3));
        border:0;
        font-size: 14px;
        padding:10px 20px 9px;
        border-radius: 20px;
        margin:8px 0 0 15px;
        &:hover{
            background: linear-gradient(rgba(255,255,255,0.15), rgba(255,255,255,0.15));
        }
    }
}

.mobile-personal-sidebar .premium{
    border-radius: 10px;
    background: #152c5f;
    color:#fff;
    margin:10px 10px 40px;
    padding:20px 10px;
    .img{
        content: url('../../assets/Business/img1.png');
        width: 45%;
    }
    h5{
        font-size: 14px;
        font-weight: normal;
        margin:13px 0;
    }
    .item{
        display: flex;
        align-items: center;
        margin:8px 0;
        svg{
            font-size: 15px;
            margin-right: 5px;
        }
        span{
            font-size: 12px;
        }
    }
    button{
        background: #427cff;
        color:#fff;
        border:0;
        cursor: pointer;
        border-radius: 20px;
        padding:7px 20px 8px;
        font-size: 14px;
        margin-top: 10px;
    }
}

.mobile-personal-sidebar .appearance-row{
    border-top:1px solid #ccc;
    padding:15px 0;
    margin:15px 0px 30px;
    button{
        width: 50%;
        font-size: 12px;
        border:0;
        background: transparent;
        cursor: pointer;
        color:#eee;
    }
}

//mode container===============
.mobile-personal-sidebar .mode-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    background: linear-gradient(rgba(0,0,0,0.2), rgba(0,0,0,0.2));
    padding:14px 0;
    a{
        margin:0 20px;
        color:#fff;
        display: flex;
        align-items: center;
        font-size: 15px;
        svg{
            font-size: 17.5px;
            margin-right: 7.5px;
        }
    }

}

.mobile-personal-sidebar .switch-mode{
    position: relative;
    margin:0 20px;
    .dark-icon{
        top:50%;
        left:50%;
        transform: translate(-50%, -50%);
        position: absolute;
        background-color: #fff;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        color:#2B4173;
        padding:0 0 1.5px 1.5px;
        width: 25px;
        height: 25px;
        font-size: 16px;
    }
    input{
        opacity: 0;
    }

    input:checked + .dark-icon{
        background: #253864;
        color:#f5e834;
    }
}


@media(max-width:930px){
    .mobile-personal-sidebar{
        display: block;
    }   
}

@-webkit-keyframes mobile-nav-scroll {
    0% {
       -webkit-transform: translateX(-100%);
       transform: translateX(-100%);
     }
     100% {
         -webkit-transform: translateX(0%);
         transform: translateX(0%);
     }
 }
 @keyframes mobile-nav-scroll {
     0% {
         -webkit-transform: translateX(-100%);
         transform: translateX(-100%);
       }
       100% {
           -webkit-transform: translateX(0%);
           transform: translateX(0%);
       }
 }

.mobile-personal-sidebar .appearance-group{
    background-color: #fff;
    border:1px solid #ddd;
    position: fixed;
    left: 50%;
    top:50%;
    transform: translate(-50%, -50%);
    z-index: 2000 !important;
    padding:25px;
    border-radius: 15px;
    width: 50%;
    h1{
        font-size: 25px;
    }
    .p{
        font-size: 17px;
        margin:10px 0;
    }
    .category{
        margin:30px 0;
    }
    .category-title{
        font-size: 17px;
    }
    .options{
        display: flex;
        justify-content: space-between;
        margin-top: 10px;
    }
    .option{
        border:1px solid #ccc;
        padding:10px;
        width: 30%;
        border-radius: 6px;
        .img{
            width: 100%;
        }
    }
    .inline{
        display: flex;
        align-items: center;
        margin-bottom: 15px;
        input{
            transform: scale(1.35);
            margin-right: 10px;
        }
        .name{
            font-size: 17px;
        }
    }
    .btns{
        display: flex;
        flex-wrap: wrap;
        button{
            border:0;
            font-size: 17px;
            font-weight: bold;
            margin:5px 10px 5px 0;
            border-radius: 5px;
            padding:12.5px 17.5px;
            cursor: pointer;
        }
        .btn1{
            background: none;
        }
        .btn2{
            background: #427cff;
            color:#fff;
        }
    }
}

@media(max-width:1401px){
    .mobile-personal-sidebar .appearance-group{
        padding:20px;
        border-radius: 10px;
        h1{
            font-size: 20px;
        }
        .p{
            font-size: 15px;
        }
        .category{
            margin:20px 0;
        }
        .category-title{
            font-size: 15px;
        }
        .options{
            flex-wrap: wrap;
            margin-top: 8px;
        }
        .option{
            width: 31%;
        }
        .inline{
            margin-bottom: 15px;
            input{
                transform: scale(1.25);
                margin-right: 10px;
            }
            .name{
                font-size: 15px;
            }
        }
        .btns{
            button{
                font-size: 14px;
                margin:3px 10px 3px 0;
                padding:10px 17.5px;
            }
        }
    }
}

@media(max-width:991px){
    .mobile-personal-sidebar .appearance-group{
        width: 75%;
    }
}


@media(max-width:767px){
    .mobile-personal-sidebar .appearance-group{
        width: 250px;
        max-width: 90%;
        height: 75vh;
        overflow-y: auto;
        border-radius: 7.5px;
        .option{
            width: 100%;
            margin:10px 0;
        }
        .options{
            margin-top: 0px;
        }
        &::-webkit-scrollbar {
            width: 7px;
        }
        &::-webkit-scrollbar-track {
            background: #e2e4e7;
            border-radius: 15px;
        }
        &::-webkit-scrollbar-thumb {
            background-color:  #a3a9c4;
            border-radius: 15px;
        }
        .btns{
            button{
                margin:3px 0;
            }
            .btn1{
                padding: 10px 0;
            }
        }
    }
}

body.dark-mode{
    .mobile-personal-sidebar .appearance-group{
        background-color: #091531;
        border:1px solid #091531;
        h1, .p, .category-title{
           color:#fff;            
        }
        .option{
            border:1px solid #ddd;
        }
        .inline{
            .name{
                color:#fff;  
            }
        }
        .btns{
            .btn1{
                color:#fff;
            }
        }
    }
}