.security-dashboard-page .all-items-dashboard {
    display: flex;
    flex-wrap: wrap;
    .left{
        width: 75%;
        padding:3.5%;
    }
}

.security-dashboard-page .all-items-dashboard .left .row1{
    h1{
        font-size: 32.5px;
        margin-bottom: 20px;
    }
    .chart-container{
        display: flex;
        flex-wrap: wrap;
        border:1px solid #ddd;
        border-radius: 10px;
        overflow: hidden;
    }
}

.security-dashboard-page .left .row1 .col1{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 20%;
    padding:15px;
    p{
        font-size: 15px;
        text-align: center;
        color:#444;
    }
    h5{
        font-size: 18px;
        color:#F4C935;
        text-align: center;
        margin:10px 0 15px;
    }
    .chart{
        position: relative;
        width: 80%;
        margin:0 auto;
    }
    .img{
        content: url('../../assets/PersonalSecurity/bg.png');
        width: 100%;
    }
    .block{
        text-align: center;
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%, -50%);
    }
    h6{
        font-size: 30px;
        margin-bottom: 6px;
    }
    small{
        font-size: 13px;
        color:#444;
    }
}

.security-dashboard-page .left .row1 .col2{
    flex: 1;
    background: #f8f8f8;
    padding:40px 25px 15px;
    .row2{
        display: flex;
        justify-content: space-between;
    }
    .first{
        width: 35%;
        h4{
            font-weight: normal;
            font-size: 25px;
        }
        h3{
            font-size: 40px;
            margin:15px 0;
        }
        p{
            font-size: 17px;
        }
    }
    .item1{
        width: 13%;
        .circle{
            width: 100%;
            padding-top: 100%;
            border-radius: 50%;
            position: relative;
        }
        h2{
            position: absolute;
            left:50%;
            top:50%;
            transform: translate(-50%, -50%);
            font-size: 45px;
            font-weight: normal;
        }
        h6{
            font-size: 17px;
            text-align: center;
            margin:15px 0 0;
        }
    }
    .red{
        border:2px solid #F1464B;
    }
    .green{
        border:2px solid #36EC5A;
    }
    .yellow{
        border:2px solid #F5D051;
    }
    .link-cont{
        margin-top: 25px;
        display: flex;
        justify-content: flex-end;
        a{
            background: #427cff;
            color:#fff;
            border-radius: 22.5px;
            font-size: 17px;
            padding:12px 25px;
        }
    }
}

@media(max-width:1401px){
    .security-dashboard-page .all-items-dashboard .left .row1{
        h1{
            font-size: 25px;
            margin-bottom: 15px;
        }
    }
    
    .security-dashboard-page .left .row1 .col1{
        width: 22%;
        padding:10px;
        p{
            font-size: 13px;
        }
        h5{
            font-size: 15px;
        }
        .chart{
            width: 90%;
        }
        h6{
            font-size: 25px;
            margin-bottom: 5px;
        }
        small{
            font-size: 11px;
        }
    }
    
    .security-dashboard-page .left .row1 .col2{
        padding:35px 20px 12px;
        .row2{
            flex-wrap: wrap;
        }
        .first{
            h4{
                font-size: 17px;
            }
            h3{
                font-size: 35px;
                margin:10px 0;
            }
            p{
                font-size: 14px;
            }
        }
        .item1{
            width: 14.5%;
            h2{
                font-size: 35px;
            }
            h6{
                font-size: 14px;
                margin:15px 0 0;
            }
        }
        .link-cont{
            margin-top: 20px;
            a{
                border-radius: 20px;
                font-size: 15px;
                padding:10px 25px;
            }
        }
    }
}

@media(max-width:991px){
    .security-dashboard-page .all-items-dashboard {
        .left{
            width: 100%;
            padding:35px 5%;
        }
    }
}

@media(max-width:815px){
    .security-dashboard-page .all-items-dashboard .left .row1{
        h1{
            font-size: 20px;
            margin-bottom: 10px;
        }
    }
    
    .security-dashboard-page .left .row1 .col1{
        width: 155px;
        max-width: 100%;
        margin:0 auto;
        padding:15px;
    }
    
    .security-dashboard-page .left .row1 .col2{
        padding:20px;
        width: 100%;
        flex: unset;
        .row2{
            flex-wrap: wrap;
        }
        .first{
            width: 100%;
            text-align: center;
            margin-bottom: 20px;
        }
        .item1{
            width: 47.5%;
            margin:7.5px 0;
            h2{
                font-size: 40px;
            }
            h6{
                margin:10px 0 0;
            }
        }
        .link-cont{
            justify-content: center;
        }
    }
}

//====================================================
.security-dashboard-page .all-items-dashboard .row22{
    margin:5% 0;
    h1{
        font-size: 27.5px;
    }
    .p{
        font-size: 17px;
        margin: 10px 0;
    }
    .react-tabs__tab-list{
        display: flex;
    }
    .react-tabs__tab{
        display: flex;
        align-items: center;
        border:1px solid #ddd;
        cursor: pointer;
        color:#555;
        font-size: 17px;
        padding:12.5px 20px;
        border-radius: 10px 10px 0 0;
        svg{
            color:#777;
            margin-right: 10px;
        }
        p{
            flex: 1;
        }
    }
    .react-tabs__tab--selected{
        background: #427cff;
        border:1px solid #427cff;
        p,svg{
            color:#fff;
        }
    }
    .tab-panel{
        border:1px solid #ddd;
        border-radius: 0 10px 10px 10px;
    }
    .message{
        font-size: 18px;
        margin:15px 0 10px 25px;
        span{
            font-weight: bold;
        }
    }
    .item2{
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        margin:10px 0;
        padding:10px 30px;
        p{
            font-size: 17px;
            margin:7.5px 10px;
        }
        span{
            border-radius: 5px;
            padding:10px 20px;
            font-size: 17px;
            margin:7.5px 10px;
            color:#fff;
        }
        .red{
            background: #FF4444;
        }
        .green{
            background: #49C978;
        }
    }
}

@media(max-width:1401px){
    .security-dashboard-page .all-items-dashboard .row22{
        margin:7.5% 0 5%;
        h1{
            font-size: 22.5px;
        }
        .p{
            font-size: 15px;
            margin: 10px 0;
        }
        .react-tabs__tab-list{
            flex-wrap: wrap;
        }
        .react-tabs__tab{
            font-size: 15px;
            padding:10px 15px;
            svg{
                margin-right: 7.5px;
            }
        }
        .message{
            font-size: 15px;
            margin:15px 0 10px 20px;
        }
        .item2{
            margin:5px 0;
            padding:7.5px 30px;
            p{
                font-size: 15px;
            }
            span{
                padding:7.5px 20px;
                font-size: 15px;
            }
        }
    }
}

@media(max-width:815px){
    .security-dashboard-page .all-items-dashboard .row22{
        margin:45px 0 5%;
        h1{
            font-size: 20px;
        }
        .react-tabs__tab{
            padding:0;
            border: 0;
            margin:7.5px 10px;
        }
        .react-tabs__tab--selected{
            background: none;
            p, svg{
                color:#427cff;
            }
        }
        .tab-panel{
            border-radius: 10px;
            margin-top: 10px;
        }
        .message{
            margin:15px;
        }
        .item2{
            margin:7.5px 10px;
            padding:0;
            p{
                font-size: 15px;
                margin:7.5px 5px;
            }
            span{
                margin:7.5px 5px;
            }
        }
    }
}

body.dark-mode{
    .security-dashboard-page .all-items-dashboard .left .row1{
        h1{
            color:#fff;
        }
        .chart-container{
            border:1px solid #ccc;
        }
    }
    
    .security-dashboard-page .left .row1 .col1{
        p{

            color:#ccc;
        }
        h6{
            color:#fff;
        }
        small{
            color:#ccc;
        }
    }
    
    .security-dashboard-page .left .row1 .col2{
        background: #2C3E66;
        .first{
            h4, h3, p{
               color:#fff; 
            }
        }
        .item1{
            h2, h6{
               color:#fff;
            }
        }
    }

    .security-dashboard-page .all-items-dashboard .row22{
        h1,.p{
           color:#fff;
        }
        .react-tabs__tab{
            svg,p{
                color:#ddd;
            }
        }
        .react-tabs__tab--selected{
            p,svg{
                color:#fff;
            }
        }
        .message{
            color:#fff;
        }
        .item2{
            p{
                color:#fff;
            }
        }
    }
}