.account-settings-page .account-setting-component{
    padding:3.5%;
}

.account-settings-page .row1 .inline{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 35px;
    h1{
        font-size: 30px;
    }
    button{
        background: #427cff;
        color:#fff;
        padding: 13px 30px 12px;
        font-size: 17px;
        border-radius: 22px;
        border:0;
        cursor: pointer;
    }
}

.account-settings-page .row1 .react-tabs{
    .react-tabs__tab-list{
        display: flex;
        border-bottom: 1px solid #ccc;
    }
    .react-tabs__tab{
        display: flex;
        align-items: center;
        padding:10px 5px;
        font-size: 17px;
        margin-right: 20px;
        color:#444;
        cursor: pointer;
        border-bottom: 3px solid transparent;
        margin-bottom: -2px;
        svg{
            font-size: 20px;
            margin-right: 5px;
            color:#666;
        }
        p{
            flex: 1;
        }
    }
    .react-tabs__tab--selected{
        color:#111;
        border-bottom: 3px solid #427cff;
        svg{
            color:#427cff;
        }
    }
    .p{
        font-size: 17px;
        margin:15px 0;
    }
    .tab2{
        .item11{
            display: none;
        }
    }
    .tab3{
        .item22{
            display: none;
        }
    }
}

.account-settings-page .account-setting-component .item1{
    margin:35px 0;
    h4{
        display: flex;
        align-items: center;
        color:#222;
        background: #fbfbfb;
        border-radius: 5px;
        padding: 15px 20px;
        font-size: 20px;
        margin-bottom: 25px;
        svg{
            font-size: 22.5px;
            margin-right: 15px;
        }
    }
}

.account-settings-page .account-setting-component .mini-item1{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin:10px 0 10px 20px;
    a{
        font-size: 27.5px;
        color:#427cff;
    }
    .left-info{
        flex: 1;
        margin-right: 20px;
        display: flex;
        align-items: center;
    }
    h5{
        width: 17.5%;
        font-size: 16px;
        margin:10px 15px 10px 0;
        color:#222;
    }
    .inputs{
        display: flex;
        align-items: center;
        p{
            font-size: 15px;
            margin:10px;
        }
    }
    input{
        height: 40px;
        border:1px solid #ddd;
        border-radius: 5px;
        font-size: 16px;
        width: 300px;
        padding:0 10px;
        color:#333;
        margin:10px;
        &:focus{
            outline: none;
        }
    }
    button{
        height: 40px;
        border:1px solid #eee;
        border-radius: 5px;
        font-size: 16px;
        color:#333;
        padding:0 20px;
        background: #fafafa;
        cursor: pointer;
        margin:10px;
    }
    .css-1pahdxg-control,.css-2b097c-container, .css-yk16xz-control {
        box-shadow: none !important;
        border-color: hsl(0, 0%, 80%) ;
        width: fit-content;
        font-size: 15px;
    }
    .css-1pahdxg-control{
        width: 100%;        
    }
    .css-2b097c-container{
        width: 300px;
        margin:10px;
    }
    .css-yk16xz-control{
        width: 100%;

    }
    .css-tlfecz-indicatorContainer,
    .css-1gtu0rj-indicatorContainer{
        margin:0 -5px !important;
    }
    .css-g1d714-ValueContainer{
        height: 40px;
        svg{
            margin-right: 10px;
        }
    }
    .checkboxes .inner-row{
        margin:3px 0;
        display: flex;
        align-items: center;
        input{
            height: 20px;
            width: 20px;
        }
        label{
            font-size: 15px;
        }
    }
}

.account-settings-page .switch-container{
    display: flex;
    align-items: center;
    margin:10px;
    .switch {
        position: relative;
        display: inline-block;
        width: 50px;
        height: 22.5px;
    }
      
    .switch input {
        opacity: 0;
        width: 0;
        height: 0;
    }
    .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #ddd;
        -webkit-transition: .4s;
        transition: .4s;
    }
      
    .slider:before {
        position: absolute;
        content: "";
        height: 16px;
        width: 16px;
        left: 3px;
        bottom: 3px;
        background-color: white;
        -webkit-transition: .4s;
        transition: .4s;
    }
      
    input:checked + .slider {
        background-color: #427cff;
    }
    input:checked + .slider:before {
        -webkit-transform: translateX(28px);
        -ms-transform: translateX(28px);
        transform: translateX(28px);
    }
    .slider.round {
        border-radius: 25px;
    }
      
    .slider.round:before {
        border-radius: 50%;
    }
    .text{
        font-size: 15px;
        margin-left: 10px;
    }
}

.account-settings-page .advanced h1{
    font-size: 30px;
    margin:50px 0 10px;
}

.account-settings-page .account-setting-component .buttons{
    display: flex;
    align-items: center;
    padding:35px 20px 5px;
    margin:25px 0 0;
    border-top: 1px solid #ddd;
    button{
        font-size: 17px;
        padding:12.5px 30px;
        border-radius: 25px;
        cursor: pointer;
        border:1px solid #427cff;
    }
    .btn1{
        background: #fff;
        color:#427cff;
        margin-right: 15px;
    }
    .btn2{
        background: #427cff;
        color:#fff;
    }
}

