                margin:7.5px;
            }
        }
        input{
            height: 35px;
            font-size: 13px;
            width: 225px;
            margin:7.5px;             
        }
        button{
            height: unset;
            font-size: 14px;
            padding:7.5px 20px;
            margin:7.5px;
        }
        .css-1pahdxg-control,.css-2b097c-container, .css-yk16xz-control {
            font-size: 13px;
        }
        .css-2b097c-container{
            width: 225px;
            margin:7.5px;
        }
        .css-g1d714-ValueContainer{
            height: 35px;
            svg{
                margin-right: 7.5px;
            }
        }
        .checkboxes .inner-row{
            margin:1px 0;
            input{
