                width: 17.5px;
            }
            label{
                font-size: 13px;
            }
        }
    }
    
    .account-settings-page .switch-container{
        margin:7.5px;
        .switch {
            width: 45px;
            height: 20px;
        }          
        .slider:before {
            height: 15px;
            width: 15px;
            left: 3px;
            bottom: 2.5px;
        }
        input:checked + .slider:before {
            -webkit-transform: translateX(25px);
            -ms-transform: translateX(25px);
            transform: translateX(25px);
        }
        .text{
            font-size: 13px;
            margin-left: 7.5px;
        }
    }
    
    .account-settings-page .advanced h1{
        font-size: 25px;
        margin:40px 0 10px;
    }
    
    .account-settings-page .account-setting-component .buttons{
        padding:30px 15px 5px;
        margin:20px 0 0;
        button{
            font-size: 15px;
            padding:11px 25px 10px;
        }
    }
}

